.pokemon-namee >span{
    font-size: x-large;
  
    letter-spacing: 0.5rem;
}
.pokemon-namee{
    font-size: x-large;
    margin-top: 1rem;
}
.pokemon-detail{
    margin-top: 1rem;
    width: 30%;
    height: 450px;
    align-items: center;
   display: flex;
   flex-direction: column;
   background-color: rgb(210, 209, 209);
   border-radius: 10px;
}
.pokemon-types{
    display: flex;
    margin-top: 1rem;
    gap: 3rem;
    font-size: large;

}
.pokemon-types div{
border: 1px solid gold;
border-radius: 50px;
padding: 1rem;
background-color: rgb(184, 184, 184);
}
