.pokedex-wrapper {
    display: flex;
    flex-direction: column;
    background-color: rgb(61, 54, 123);
    border-radius: 10px;
    
}

div .button{
   width: 70px;
    height: 50px; 
    margin-left: 1rem;
    border-radius: 10px;
    font-size: larger;
   cursor: pointer;
   border: solid black ;
   background: linear-gradient(to right, #3498db, #e74c3c);
  
   background-size: 200% 100%; 

  

}
@keyframes gradientShift {
    0% {
        background-position: 200% 0;
    }
    100% {
        background-position: -200% 0;
    }}

 .button:hover{
    background-position: -100% 0;
    animation: gradientShift 5s linear infinite;
  
}
div .main-button{
    text-align: center;
    margin-top: 30px;
    padding: 20px;
   
   
}
