.pokemon{
    margin-top: 2rem;
    flex-basis: 20%;
    width: 500px;
    height: 250px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.pokemon-image{
    height: 200px;
    max-width: 200px;
    margin-top: 1rem;
}
.pokemon-name{
    font-size: larger;
    letter-spacing: 0.3rem;
    color: rgb(12, 255, 255); 
}
.pokemon :hover{
    background-color: cadetblue;
    border-radius: 5px;
}